import React, { useState } from 'react';
import './Projects.css';
import { FaChevronLeft } from "@react-icons/all-files/fa/FaChevronLeft";
import { FaChevronRight } from "@react-icons/all-files/fa/FaChevronRight";

const Projects = () => {

  const [currentViews, setCurrentViews] = useState(new Array(2).fill(0)); 
  const [currentPage, setCurrentPage] = useState(0);
  const projectsPerPage = 3;
  const projects = [
    {
      title: "Couple Photo Map",
      date: "May 2024",
      description: [],
      details: [
        "Developed an iOS application that enables couples to share and organize their photos based on location",
        "Implemented real-time photo sharing and location tracking using Firebase Realtime Database",
        "Integrated Google Maps API to display photo locations and create an interactive map experience",
        "Built a custom photo gallery with location-based filtering and sorting capabilities",
        "Utilized Firebase Storage for efficient photo management and Cloud Firestore for user data"
      ],
      images: [],
      technologies: ["Swift", "Firebase", "Google Maps API", "UIKit", "Core Location"],
      github: "https://github.com/sjlee2016/photo-map"
    },
    {
      title: "The Dialog System Technology Challenge",
      date: "December 2020",
      description: [
      ],
      details: [
        "Placed 1st place in track 4 of DSTC9",
        "Improved Facebook research team's situated interactive multi-modal conversational AI",
        "Paper accepted at AAAI 2021 workshop",
        "Modified GPT-2 libraries to enable ensemble learning",
        "Improved response accuracy from 69% to 79%",
        "Co-authored paper: 'End-to-End Task-Oriented Multimodal Dialog System with GPT-2'"
      ],
      images: [
      ],
      technologies: ["Python", "Machine Learning", "LLM", "GPT-2"],
      github: "https://drive.google.com/file/d/1_-F_7mGcmmIm7S5IyH-puVZM5uelp7nu/view"
    },
    {
      title: "Calligram",
      date: "November 2020",
      description: [
      
      ],
      details: [
        "Developed a deep neural network model for recognizing full-page Korean handwriting documents",
        "Received Creative Award from HCI Korea 2021",
        "Achieved 98% text recognition accuracy for training data",
        "Achieved 75% accuracy for testing data",
        "Implemented full-page recognition service using OrigamiNet Models"
      ],
      images: [],
      technologies: ["Python", "Computer Vision", "Django", "Deep Learning"],
      github: "https://github.com/sjlee2016/calligram"
    },
    {
      title: "Loot Game Store Platform",
      date: "September 2019",
      description: [
      
      ],
      details: [
        "Developed and deployed a new feature that enabled comprehensive tracking of user activities and purchase histories within the game store, enhancing user engagement and monetization",
        "Designed and wrote efficient shell scripts to automate the wrapping process of over 9,000 mobile games with Playphone Inc's SDK, resulting in significant improvements in productivity and efficiency",
        "Implemented Jenkins as a continuous integration service to automate the entire SDK wrapping process",
      ],
      images: [],
      technologies: ["Java", "Spring Boot", "MySQL", "AWS", "Python", "Jenkins", "Docker", "Shell Scripting"],
      github: ""
    }
  ];

  const nextView = (index) => {
    const project = projects[index];
    if (!project.images) return;
    
    setCurrentViews(prev => {
      const newViews = [...prev];
      newViews[index] = (newViews[index] + 1) % (project.images.length + 1);
      return newViews;
    });
  };

  const prevView = (index) => {
    const project = projects[index];
    if (!project.images) return;
    
    setCurrentViews(prev => {
      const newViews = [...prev];
      newViews[index] = (newViews[index] - 1 + (project.images.length + 1)) % (project.images.length + 1);
      return newViews;
    });
  };

  const nextPage = () => {
    setCurrentPage(prev => 
      (prev + 1) * projectsPerPage >= projects.length ? 0 : prev + 1
    );
  };

  const prevPage = () => {
    setCurrentPage(prev => 
      prev === 0 ? Math.floor((projects.length - 1) / projectsPerPage) : prev - 1
    );
  };

  const getCurrentProjects = () => {
    const start = currentPage * projectsPerPage;
    return projects.slice(start, start + projectsPerPage);
  };

  const renderContent = (project, index) => {
    if (!project.images) {
      return (
        <ul className="project-details-list">
          {project.details.map((detail, i) => (
            <li key={i}>{detail}</li>
          ))}
        </ul>
      );
    }

    if (currentViews[index] === 0) {
      return (
        <ul className="project-details-list">
          {project.details.map((detail, i) => (
            <li key={i}>{detail}</li>
          ))}
        </ul>
      );
    }

    return (
      <div className="project-image-container">
        <img 
          src={project.images[currentViews[index] - 1]} 
          alt={`${project.title} view ${currentViews[index]}`}
          className="project-image"
        />
      </div>
    );
  };

  return (
    <section id="projects" className="projects-section">
      <h3>Notable Projects</h3>
      <div className="projects-container">
        <button 
          className="projects-nav-button prev"
          onClick={prevPage}
        >
          <FaChevronLeft />
        </button>

        <div className="projects-grid">
          {getCurrentProjects().map((project, index) => (
            <div className="project-card" key={index}>
              <div className="project-header">
                <h4>{project.title}</h4>
                <span className="project-date">{project.date}</span>
              </div>
              <ul className="project-description-list">
                {project.description.map((desc, i) => (
                  <li key={i}>{desc}</li>
                ))}
              </ul>
              
              <div className="project-content">
                {project.images?.length > 0 && (
                  <button 
                    className="nav-button prev"
                    onClick={() => prevView(index)}
                  >
                    <FaChevronLeft />
                  </button>
                )}

                <div className="content-wrapper">
                  {renderContent(project, index)}
                </div>

                {project.images?.length > 0 && (
                  <button 
                    className="nav-button next"
                    onClick={() => nextView(index)}
                  >
                    <FaChevronRight />
                  </button>
                )}
              </div>

              <div className="tech-stack">
                {project.technologies.map((tech, i) => (
                  <span key={i} className="tech-tag">{tech}</span>
                ))}
              </div>

              <div className="project-links">
                <a href={project.github} target="_blank" rel="noopener noreferrer" className="github-link">
                  View Project
                </a>
              </div>
            </div>
          ))}
        </div>

        <button 
          className="projects-nav-button next"
          onClick={nextPage}
        >
          <FaChevronRight />
        </button>
      </div>
    </section>
  );
};

export default Projects;